import { Component, OnInit } from '@angular/core';
import { Message } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { CusOperationHttpService } from '../../../http/custom-operation/custom-operation.http.service';
import { CustManageBean } from './bean/custManage.bean';
import { Router } from '@angular/router';
import { tableMessage } from 'app/@uisftech/common/constant/config';
/**
 * 客户经理活动进展查询
 */
@Component({
	selector: 'app-custom-manager',
	templateUrl: './custom-manager.component.html',
	styleUrls: ['./custom-manager.component.scss'],
	providers: [CusOperationHttpService, Commfunc]
})
export class CustomManagerComponent implements OnInit {
	title: string = "客户经理进展查询";
	arr: any = [];
	tabMesg: any = tableMessage;//表格中无数据时显示的内容
	//提示信息
	msgs: Message[] = [];
	ch: any = CALENDAR_CN;
	temData: Date = new Date();
	custManageBean: CustManageBean = new CustManageBean;
	fromDate: Date = null;
	thruDate: Date = null;
	tableData: any = [];
	totalNum: number;
	first: number = 0;//分页控制
	//每页多少条
	pageSize: number = 10;
	campaignReleaseOrgName: string = '';
	campaignReleaseOrgId: string = '';
	//机构树模态框
	orgTreeDisplay: boolean = false;//模态框是否展示
	custMajorName: string;
	showFlag: boolean = true;
	ystatus: any = [];
	personPage: boolean = false;
	userInValue: string;
	userId: string;
	personName: string;
	seqFlag: any = [];
	code: any;
	contactEquity: any = [];
	whetherInterested: any = [];
	constructor(
		private httpService: CusOperationHttpService,
		private commfunc: Commfunc,
		private router: Router,
	) {
		this.codeValues() //调用方法，获取全部码值
		this.seqFlag = this.code['SEQFlAG'];//营销渠道标识
		this.whetherInterested = this.code['WHETHERINTERESTED'];//感兴趣与否
		this.contactEquity = this.code['CONTACTEQUITY'];//联系情况
	}

	ngOnInit() {
		this.custManageBean.pageNum = 1;
		this.custManageBean.pageSize = 10;
		this.first = 0;
		this.queryClick();
	}
	//码值
	codeValues() {
		this.commfunc.codeValue();
		let ret = this.commfunc.codeDatas;
		return this.code = ret //code是自己声明的变量
	}
	//查询
	queryClick() {
		this.query();
	}
	//查询接口
	query() {
		this.custManageBean.fromDate = this.commfunc.transDateN(this.fromDate);
		this.custManageBean.thruDate = this.commfunc.transDateN(this.thruDate);
		let ob = this.httpService.queryManagerCampaignInfo(this.custManageBean);
		ob.subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.tableData = data.returnList;
				this.totalNum = data.totalNum;
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
		})
	}
	//重置
	reClean() {
		this.custMajorName = '';
		this.custManageBean = new CustManageBean();
		this.fromDate = null;
		this.thruDate = null;
		this.campaignReleaseOrgId = '';
		this.campaignReleaseOrgName = '';
		this.querySecleReset();
	}

	//页码重置
	querySecleReset() {
		this.custManageBean.pageSize = 10;
		this.custManageBean.pageNum = 1;
		this.first = 0;
	}
	//分页
	paginate(event) {
		//每页显示的条数
		this.custManageBean.pageSize = event.rows;
		//当前页
		let currentPage = event.page + 1;
		this.custManageBean.pageNum = currentPage;
		this.first = (currentPage - 1) * this.custManageBean.pageSize;

		//调用查询的方法
		this.query();
	}
	//选择机构
	chooseOrg() {
		document.body.style.overflow = 'hidden';
		this.orgTreeDisplay = !this.orgTreeDisplay;
	}

	//机构树回调
	treeCall(param) {
		document.body.style.overflow = 'auto';
		this.orgTreeDisplay = false;
		this.custMajorName = param.orgName;
		this.custManageBean.orgId = param.orgId;
	}
	//选择员工
	chooseWorker() {
		document.body.style.overflow = 'hidden';
		if (this.custManageBean.orgId == '' || this.custManageBean.orgId == null || this.custManageBean.orgId == undefined) {
			// this.msgs = [];
			// this.msgs.push({ severity: 'error', summary: '提示', detail: '请先选择所属机构！' });
			// return
			this.userInValue ='0000';
		}else{
			this.userInValue = this.custManageBean.orgId;
		}
		this.personPage = !this.personPage;
	}

	//员工回调
	perInfor(item) {
		document.body.style.overflow = 'auto';
		this.personPage = false;
		if (item.length > 0) {
			this.custManageBean.userId = item[0].tellerId;
			this.campaignReleaseOrgId = this.custManageBean.userId;
			this.custManageBean.userName = item[0].tellerName;
			this.campaignReleaseOrgName = this.custManageBean.userName;
		}
	}
	//开始日期
	showDataStart() {
		if (this.fromDate && this.thruDate) {
			if (this.fromDate > this.thruDate) {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
				setTimeout(() => {
					this.fromDate = null;
				})
			}
		}
	}
	//结束日期
	showDataEnd() {
		if (this.fromDate && this.thruDate) {
			if (this.fromDate > this.thruDate) {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
				setTimeout(() => {
					this.thruDate = null;
				})
			}
		}
	}
	//手动输入日期时，失去焦点时进行判断
	onchange1() {
		this.showDataStart()
	}
	onchange2() {
		this.showDataEnd()
	}
}
