import { Component, OnInit } from '@angular/core';
import { CusOperationHttpService } from '../../../http/custom-operation/custom-operation.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Router } from '@angular/router';
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config';
// import {tableMessage} from 'app/@uisftech/common/constant/config';
import { Message } from 'primeng/primeng';
import { CustProgressBean } from './bean/custprogress.bean';

/***
 * 客户活动进展查询
 */

@Component({
	selector: 'app-custom-progress',
	templateUrl: './custom-progress.component.html',
	styleUrls: ['./custom-progress.component.scss'],
	providers: [CusOperationHttpService, Commfunc]
})
export class CustomProgressComponent implements OnInit {
	custProgressBean: CustProgressBean = new CustProgressBean()
	//总页数
	totalNum: number = 0;
	tabMesg: any = tableMessage;//表格中无数据时显示的内容
	//提示信息
	msgs: Message[] = [];
	ch: any = CALENDAR_CN;
	temData: Date = new Date();
	title: string = "客户活动进展查询";
	arr: any = [];//表头数据
	tableData: any = [];//表数据
	selectedCities: any = [];//单选框
	checked: boolean = false;//全选按钮
	selectDate: any = [];//单选框的数据
	fromDate: Date = null;
	thruDate: Date = null;
	campaignReleaseOrgName: string = '';
	campaignReleaseOrgId: string = '';
	showFlag: boolean = true;
	//机构树模态框
	orgTreeDisplay: boolean = false;//机构树的模态框是否展示
	display: boolean = false;//详情的模态框
	first: number = 0;//分页控制
	//每页多少条
	pageSize: number = 10;
	custMajorName: string;
	custMajorOrgId: string;
	inValue: any;
	personPage: boolean = false;
	userInValue: string;
	personName: string;
	ystatus: any = [];
	code: any;
	seqFlag: any = [];
	status: any = [];
	CampaignSuccess: any = [];
	contactEquity: any = [];
	serviceTypeList: any = []; // 交易类型

	tableHeaderList: any = [];
	tableResultList: any = [];
	tempData: any = [];
	sfList: any = [];
	contactWay: any = [];

	constructor(
		private httpService: CusOperationHttpService,
		private commfunc: Commfunc,
		private router: Router,

	) {
		this.codeValues() //调用方法，获取全部码值
		this.seqFlag = this.code['MarkingSeqFlag'];//营销渠道标识
		this.status = this.code['MarketingStatus2'];
		this.CampaignSuccess = this.code['MarketingWtherInterest'];// 客户意图
		this.contactEquity = this.code['CONTACTEQUITY']; // 联系情况
		this.serviceTypeList = this.code['ServiceTypeList']; // 交易类型
		this.sfList = this.code['VastRejList'];// 是否
		this.contactWay = this.code['contactWays'];// 联系方式
	}
	//码值
	codeValues() {
		this.commfunc.codeValue();
		let ret = this.commfunc.codeDatas;
		return this.code = ret //code是自己声明的变量
	}

	ngOnInit() {
		this.custProgressBean.serviceType = '12'; // 默认交易
		this.queryScale();
	}

	// 初始化表格
	queryScale() {
		this.custProgressBean.pageSize = 10;
		this.custProgressBean.pageNum = 1;
		this.first = 0;
		this.query();
	}

	//查询接口
	query() {
		this.custProgressBean.fromDate = this.commfunc.transDateN(this.fromDate);
		this.custProgressBean.thruDate = this.commfunc.transDateN(this.thruDate);
		let ob1 = this.httpService.queryCustCampaignInfo(this.custProgressBean);
		ob1.subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.tableHeaderList = data.tableHeader;
				this.tableResultList = data.resultList;
				this.totalNum = data.totalNum;
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
		})
	}

	//重置
	reClean() {
		this.personName = '';
		this.custMajorName = '';
		this.custProgressBean = new CustProgressBean();
		this.custProgressBean.serviceType = '12'; // 默认交易
		this.fromDate = null;
		this.thruDate = null;
		this.campaignReleaseOrgId = '';
		this.campaignReleaseOrgName = '';
		this.querySecleReset();
	}
	//页码重置
	querySecleReset() {
		this.custProgressBean.pageSize = 10;
		this.custProgressBean.pageNum = 1;
		this.first = 0;
	}

	paginate(event) {
		//每页显示的条数
		this.custProgressBean.pageSize = event.rows;
		//当前页
		let currentPage = event.page + 1;
		this.custProgressBean.pageNum = currentPage;
		this.first = (currentPage - 1) * this.custProgressBean.pageSize;
		//调用查询的方法
		this.query();
	}

	//选择机构
	chooseOrg() {
		document.body.style.overflow = 'hidden';
		this.orgTreeDisplay = !this.orgTreeDisplay;
	}

	//机构树回调
	treeCall(param) {
		document.body.style.overflow = 'auto';
		this.orgTreeDisplay = false;
		this.custProgressBean.orgName = param.orgName;
		this.custProgressBean.orgId = param.orgId;
	}
	//点击详情
	details(item) {
		this.commfunc.clearSession('MarkingProgressDetailParam')
		this.commfunc.setSessionDataCH('MarkingProgressDetailParam', JSON.stringify(item))
		this.router.navigate(['/pages/tzb/custom/marketing/cust-query/marking-progress-detail'])
	}
	//点击X，关闭模态框
	onHideClose() {
		document.body.style.overflow = 'auto';
		this.orgTreeDisplay = false;
	}
	//开始日期
	showDataStart() {
		if (this.fromDate && this.thruDate) {
			if (this.fromDate > this.thruDate) {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
				setTimeout(() => {
					this.fromDate = null;
				})
			}
		}
	}
	//结束日期
	showDataEnd() {
		if (this.fromDate && this.thruDate) {
			if (this.fromDate > this.thruDate) {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
				setTimeout(() => {
					this.thruDate = null;
				})
			}
		}
	}
	//手动输入日期时，失去焦点时进行判断
	onchange1() {
		this.showDataStart()
	}
	onchange2() {
		this.showDataEnd()
	}

	compileStr(cust) {
		return this.commfunc.compileStr(cust)
	}
}

