import { Component, OnInit, Input } from '@angular/core';
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config';
import { CusOperationHttpService } from '../../../../http/custom-operation/custom-operation.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Message, ConfirmationService } from 'primeng/primeng';
import { Router, ActivatedRoute } from '@angular/router';
import { MarketingHttpService } from 'app/pages/tzb/custom/marketing/http/marketing.http.service';

@Component({
	selector: 'app-marking-progress-detail',
	templateUrl: './marking-progress-detail.component.html',
	styleUrls: ['./marking-progress-detail.component.scss'],
	providers: [Commfunc, CusOperationHttpService, ConfirmationService, MarketingHttpService]
})
export class MarkingProgressDetailComponent implements OnInit {

	tabMesg: any = tableMessage;
	tablelist: any = [];
	dataList: any;
	copyData: any = {};
	typeCenter: string;
	copyDisplay: boolean = false;
	userId: string;
	releaseUserId: string;
	undisplay: boolean = false;
	// showFlag: boolean = true;
	// type: string;
	// flagSumit: boolean = false;
	msgs: Message[] = [];
	headerTitle: string;
	display: boolean = false;
	inValue: any;
	marketingCampaignId: string;
	marketingCampaigntType: string;//任务类型
	status: any = [];
	code: any;
	sfList: any = [];
	contactWay: any = [];
	serviceTypeList: any = [];
	CampaignSuccess: any = [];
	
	constructor(
		public cusOperationHttpService: CusOperationHttpService,
		public commfunc: Commfunc,
		public activatedRoute: ActivatedRoute,
		private router: Router,
		public marketingHttpService: MarketingHttpService
	) {
		this.codeValues() //调用方法，获取全部码值
		this.status = this.code['MarketingStatus'];
		this.sfList = this.code['VastRejList'];// 是否
		this.contactWay = this.code['CONTACTWAY'];// 联系方式
		this.serviceTypeList = this.code['ServiceTypeList']; // 交易类型
		this.CampaignSuccess = this.code['MarketingWtherInterest'];// 客户意图

		this.dataList = {},
		this.dataList = JSON.parse(this.commfunc.getSessionDataCH('MarkingProgressDetailParam'));
		console.log(this.dataList)
		
		// 推荐产品
		if (!this.dataList.productNo && !this.dataList.productName) {
			this.tablelist = [];
		} else {
			this.tablelist.push(
				{ seqNo:'0001',productNo: this.dataList.productNo, productName: this.dataList.productName, recommendReason: this.dataList.recommendReason }
			)
		}
	
	}

	//码值
	codeValues() {
		this.commfunc.codeValue();
		let ret = this.commfunc.codeDatas;
		return this.code = ret //code是自己声明的变量
	}

	ngOnInit() {

	}

	compileStr(cust) {
		return this.commfunc.compileStr(cust)
	}

	// 返回
	returnBtn() {
		this.router.navigate(['/pages/tzb/custom/marketing/cust-query/custom-progress'])
	}
}
