import { Component, OnInit, Input } from '@angular/core';
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config';
import { OrgProgressBean } from './bean/OrgProgress.bean';
import { CusOperationHttpService } from '../../../../http/custom-operation/custom-operation.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Message } from 'primeng/primeng';
import { Router, ActivatedRoute } from '@angular/router';
import { element } from 'protractor';
/**
 * 客户活动进展查询
 */
@Component({
	selector: 'app-org-progress-detail',
	templateUrl: './org-progress-detail.component.html',
	styleUrls: ['./org-progress-detail.component.scss'],
	providers: [CusOperationHttpService, Commfunc]
})
export class OrgProgressDetailComponent implements OnInit {
	@Input() inValue: any;
	sales: any = [];
	ch: any = CALENDAR_CN;
	tabMesg: any = tableMessage;
	orgProgressBean: OrgProgressBean = new OrgProgressBean();
	startDate: Date = null;
	thruDate: Date = null;
	tableData: any = [];
	totalNum: number;
	msgs: Message[] = [];
	orgTreeDisplay: boolean = false;
	custMajorName: string;
	personPage: boolean = false;
	userInValue: any;
	personName: string;
	orgId: string;
	// status:number;
	constructor(
		public cusOperationHttpService: CusOperationHttpService,
		public commfunc: Commfunc,
		public activatedRoute: ActivatedRoute,
	) {
		// this.status=1;
	}

	ngOnInit() {
		this.orgProgressBean.marketingCampaignId = this.activatedRoute.snapshot.params['marketingCampaignId'];
		this.orgId = this.activatedRoute.snapshot.params['orgId'];
		this.orgProgressBean.countNum = '10';
		this.orgProgressBean.pageNum = '1';
		this.queryClick();
	}
	queryClick() {
		// this.status=2;
		this.query();
	}

	//查询
	query() {
		this.orgProgressBean.startDate = this.commfunc.transDateN(this.startDate);
		this.orgProgressBean.thruDate = this.commfunc.transDateN(this.thruDate);
		if (!this.orgProgressBean.marketingCampaignId) {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '营销活动编号不能为空' });
			return;
		}
		let ob = this.cusOperationHttpService.queryMarketingResultForOrgn(this.orgProgressBean);
		ob.subscribe(data => {
			if (data.returnCode.code == 'success') {
				let campaignName=data.campaignName;
				let startDate=data.startDate;
				let monThruDate=data.monThruDate;
				if (data.resultList) {
					data.resultList.forEach((element,i) => {
						data.resultList[i].campaignName=campaignName;
						data.resultList[i].startDate=startDate;
						data.resultList[i].monThruDate=monThruDate;
					});
					this.tableData=data.resultList;
				
				}
				this.totalNum = data.totalCount;
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
		})
	}
	//重置
	reClean() {
		this.startDate = null;
		this.thruDate = null;
		this.orgProgressBean = new OrgProgressBean();
		this.custMajorName = '';
		this.personName = '';
		this.querySecleReset();
	}
	//页码重置
	querySecleReset() {
		this.orgProgressBean.countNum = '10';
		this.orgProgressBean.pageNum = '1';
	}
	//选择机构
	chooseOrg() {
		document.body.style.overflow = 'hidden';
		this.orgTreeDisplay = !this.orgTreeDisplay;
	}

	//机构树回调
	treeCall(param) {
		document.body.style.overflow = 'auto';
		this.orgTreeDisplay = false;
		this.custMajorName = param.orgName;
		this.orgProgressBean.orgId = param.orgId;
	}
	//选择员工
	chooseWorker() {
		document.body.style.overflow = 'hidden';
		if (this.custMajorName == '' || this.custMajorName == undefined) {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '请先选择所属机构！' });
			return
		}
		this.personPage = !this.personPage;
		this.userInValue = this.orgProgressBean.orgId;
		//选择了机构
		if (this.orgProgressBean.orgId != '' && this.orgProgressBean.orgId != null && this.orgProgressBean.orgId != undefined) {
			this.userInValue = this.orgProgressBean.orgId;
			return
		}
	}

	//员工回调
	perInfor(item) {
		document.body.style.overflow = 'auto';
		this.personPage = false;
		if (item.length > 0) {
			this.orgProgressBean.userId = item[0].tellerId;
			this.personName = item[0].tellerName;
		}
	}
	paginate　(event) {
		this.orgProgressBean.countNum = event.rows;
		let currentPage = (event.page + 1).toString();
		this.orgProgressBean.pageNum = currentPage;
		this.query();
		this.orgProgressBean.pageNum = '1';
	}
	//开始日期
	showDataStart() {
		if (this.startDate && this.thruDate) {
			if (this.startDate > this.thruDate) {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
				setTimeout(() => {
					this.startDate = null;
				})
			}
		}
	}
	//结束日期
	showDataEnd() {
		if (this.startDate && this.thruDate) {
			if (this.startDate > this.thruDate) {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
				setTimeout(() => {
					this.thruDate = null;
				})
			}
		}
	}
	//手动输入日期时，失去焦点时进行判断
	onchange1() {
		this.showDataStart()
	}
	onchange2() {
		this.showDataEnd()
	}
	//返回按钮
	returnBtn() {
		setTimeout(() => {
			window.history.go(-1);
		}, 500)
	}
}
