import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { CustomProgressComponent } from './custom-progress/custom-progress.component';
import { CustomProgressDetailComponent } from './custom-progress/custom-progress-detail/custom-progress-detail.component';
import { OrgProgressComponent } from './org-progress/org-progress.component';
import { OrgProgressDetailComponent } from './org-progress/org-progress-detail/org-progress-detail.component';
import { CustomManagerComponent } from './custom-manager/custom-manager.component';
import { MarkingProgressDetailComponent } from 'app/pages/tzb/custom/marketing/cust-query/custom-progress/marking-progress-detail/marking-progress-detail.component';

export const routes: Routes = [
    {
        path: '',
        children: [
            {
                path: 'custom-progress',
                component: CustomProgressComponent
            },
            {
                path: 'custom-progress-detail',
                component: CustomProgressDetailComponent
            },
            {
                path: 'org-progress',
                component: OrgProgressComponent
            },
            {
                path: 'org-progress-detail',
                component: OrgProgressDetailComponent
            },
            {
                path: 'custom-manager',
                component: CustomManagerComponent
            },
            {
                path: 'marking-progress-detail',
                component: MarkingProgressDetailComponent,
            },
        ]
    }
]
export const CustQueryRouting: ModuleWithProviders = RouterModule.forChild(routes);