import { Component, OnInit } from '@angular/core';
import { Message } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { CusOperationHttpService } from '../../../http/custom-operation/custom-operation.http.service';
import { OrgBean } from './bean/orgprogress.bean';
import { Router } from '@angular/router';
import { tableMessage } from 'app/@uisftech/common/constant/config';
/**
 * 机构活动进展查询
 */
@Component({
	selector: 'app-org-progress',
	templateUrl: './org-progress.component.html',
	styleUrls: ['./org-progress.component.scss'],
	providers: [CusOperationHttpService, Commfunc]
})
export class OrgProgressComponent implements OnInit {
	tabMesg: any = tableMessage;//表格中无数据时显示的内容
	//提示信息
	msgs: Message[] = [];
	title: string = '机构活动进展查询';
	arr: any = [];//表头数据
	ch: any = CALENDAR_CN;
	orgBean: OrgBean = new OrgBean;
	fromDate: Date = null;
	thruDate: Date = null;
	tableData: any = [];
	totalNum: number;
	first: number = 0;//分页控制
	//每页多少条
	pageSize: number = 10;
	pageNum: number = 1;
	campaignReleaseOrgName: string = '';
	campaignReleaseOrgId: string = '';
	//机构树模态框
	orgTreeDisplay: boolean = false;//模态框是否展示
	showFlag: boolean = true;
	custMajorName: string;
	showModel: string;
	display: boolean = false;
	ystatus: any = [];
	seqFlag: any = [];
	code: any;
	inValue: any;
	OrgLevel:any=[];//机构层级
	constructor(
		private httpService: CusOperationHttpService,
		private commfunc: Commfunc,
		private router: Router,
	) {
		this.codeValues() //调用方法，获取全部码值
		this.seqFlag = this.code['SEQFlAG'];//营销渠道标识
		this.OrgLevel=this.code['ORGLEVEL'];//机构层级
	}
	//码值
	codeValues() {
		this.commfunc.codeValue();
		let ret = this.commfunc.codeDatas;
		return this.code = ret //code是自己声明的变量
	}
	ngOnInit() {
		this.orgBean.pageSize = 10;
		this.orgBean.pageNum = 1;
		this.queryClick();
	}


	btnDetail(item) {
		this.router.navigate(['/pages/tzb/custom/marketing/cust-query/org-progress-detail', item])
	}
	//查询
	queryClick() {
		this.orgBean.pageNum = 1;
		this.orgBean.pageSize = 10;
		this.first = 0;
		this.query();
	}
	//查询接口
	query() {
		this.orgBean.fromDate = this.commfunc.transDateN(this.fromDate);
		this.orgBean.thruDate = this.commfunc.transDateN(this.thruDate);
		let ob = this.httpService.queryOrgCampaignInfo(this.orgBean);
		ob.subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.tableData = data.resultList;
				this.totalNum = data.totalNum;
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
		})
	}
	//重置
	reClean() {
		this.orgBean = new OrgBean();
		this.fromDate = null;
		this.thruDate = null;
		this.campaignReleaseOrgId = '';
		this.campaignReleaseOrgName = '';
		this.querySecleReset();
	}
	//页码重置
	querySecleReset() {
		this.orgBean.pageSize = 10;
		this.orgBean.pageNum = 1;
		this.first = 0;
	}
	//分页
	paginate(event) {
		this.orgBean.pageSize = event.rows;
		let currentPage = event.page + 1;
		this.orgBean.pageNum = currentPage;
		this.first = (currentPage - 1) * this.orgBean.pageSize;
		this.queryClick();
		this.orgBean.pageNum = 1;
	}

	//选择机构
	chooseOrg() {
		this.showModel = '1';
		document.body.style.overflow = 'hidden';
		this.orgTreeDisplay = !this.orgTreeDisplay;
	}

	//机构树回调
	treeCall(param) {
		document.body.style.overflow = 'auto';
		this.orgTreeDisplay = false;
		this.campaignReleaseOrgName = param.orgName;
		this.orgBean.orgId = param.orgId;
		this.orgBean.orgName = param.orgName;
	}
	//开始日期
	showDataStart() {
		if (this.fromDate && this.thruDate) {
			if (this.fromDate > this.thruDate) {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
				setTimeout(() => {
					this.fromDate = null;
				})
			}
		}
	}
	//结束日期
	showDataEnd() {
		if (this.fromDate && this.thruDate) {
			if (this.fromDate > this.thruDate) {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
				setTimeout(() => {
					this.thruDate = null;
				})
			}
		}
	}
	//手动输入日期时，失去焦点时进行判断
	onchange1() {
		this.showDataStart()
	}
	onchange2() {
		this.showDataEnd()
	}
}
