import { Component, OnInit, Input } from '@angular/core';
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config';
import { CustProgressBean } from './bean/CustProgress.bean';
import { CusOperationHttpService } from '../../../../http/custom-operation/custom-operation.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Message } from 'primeng/primeng';
import { Router, ActivatedRoute } from '@angular/router';
/**
 * 客户活动进展查询详情
 */
@Component({
	selector: 'app-custom-progress-detail',
	templateUrl: './custom-progress-detail.component.html',
	styleUrls: ['./custom-progress-detail.component.scss'],
})
export class CustomProgressDetailComponent implements OnInit {
	// @Input() inValue: any;
	sales: any = [];
	ch: any = CALENDAR_CN;
	tabMesg: any = tableMessage;
	custProgressBean: CustProgressBean = new CustProgressBean();
	startDate: Date = null;
	thruDate: Date = null;
	tableData: any = [];
	totalNum: number;
	msgs: Message[] = [];
	first: number = 0;//分页控制
	orgTreeDisplay: boolean = false;
	custMajorName: string;
	personPage: boolean = false;
	userInValue: any;
	personName: string;
	custNo: string;//客户号
	custId: string;
	status:number;
	constructor(
		public cusOperationHttpService: CusOperationHttpService,
		public commfunc: Commfunc,
		public activatedRoute: ActivatedRoute,
	) {
		this.custNo = this.activatedRoute.snapshot.params['custId'];
		this.status=1;
	}

	ngOnInit() {
		this.custProgressBean.marketingCampaignId = this.activatedRoute.snapshot.params['marketingCampaignId'];
		if (this.activatedRoute.snapshot.params['orgId']) {
			this.custProgressBean.orgId = this.activatedRoute.snapshot.params['orgId'];
			this.custMajorName = this.activatedRoute.snapshot.params['orgName']
		}
		this.custProgressBean.countNum = 10;
		this.custProgressBean.pageNum = 1;
		this.query();
	}
	queryClick() {
		this.status=2;
		this.query();
	}
	query() {
		this.custProgressBean.startDate = this.commfunc.transDateN(this.startDate);
		this.custProgressBean.thruDate = this.commfunc.transDateN(this.thruDate);
		if (!this.custProgressBean.marketingCampaignId) {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '营销活动编号不能为空' });
			return;
		}
		if (!this.custProgressBean.orgId) {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '机构不能为空' });
			return;
		}
		let ob = this.cusOperationHttpService.queryMarketingResultForCustomer(this.custProgressBean);
		ob.subscribe(data => {
			if (data.returnCode.code == 'success') {
				if (data.resultList.length>0) {
					if (this.status==1) {
						data.resultList.forEach((element, i) => {
							if (element.custId == this.custNo) {
								this.tableData = [data.resultList[i]];
								console.log(this.tableData)
								this.totalNum = 1;
							} 
						});
					}else if (this.status==2) {
						this.tableData = data.resultList;
						this.totalNum = data.totalCount;
					}
				}else{
					this.tableData = data.resultList;
					this.totalNum = data.totalCount;
				}

				// data.resultList = data.resultList.filter(element => {return element.custId == this.custNo});
				// this.tableData = data.resultList;
				// this.totalNum = data.totalCount;
				// console.log(data.resultList)

			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
		})

	}
	//点击X，关闭模态框
	onHideClose() {
		document.body.style.overflow = 'auto';
		this.orgTreeDisplay = false;
		this.personPage = false;
	}
	//选择机构
	chooseOrg() {
		document.body.style.overflow = 'hidden';
		this.orgTreeDisplay = !this.orgTreeDisplay;
	}

	//机构树回调
	treeCall(param) {
		document.body.style.overflow = 'auto';
		this.orgTreeDisplay = false;
		this.custMajorName = param.orgName;
		this.custProgressBean.orgId = param.orgId;
	}
	//选择员工
	chooseWorker() {
		document.body.style.overflow = 'hidden';
		if (this.custMajorName == '' || this.custMajorName == undefined) {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '请先选择所属机构！' });
			return
		}
		this.personPage = !this.personPage;
		this.userInValue = this.custProgressBean.orgId;
		//选择了机构
		if (this.custProgressBean.orgId != '' && this.custProgressBean.orgId != null && this.custProgressBean.orgId != undefined) {
			this.userInValue = this.custProgressBean.orgId;
			return
		}
	}

	//员工回调
	perInfor(item) {
		document.body.style.overflow = 'auto';
		this.personPage = false;
		if (item.length > 0) {
			this.custProgressBean.userId = item[0].tellerId;
			this.personName = item[0].tellerName;
		}
	}
	//重置
	reClean() {
		this.startDate = null;
		this.thruDate = null;
		this.custProgressBean = new CustProgressBean();
		this.custMajorName = '';
		this.personName = '';
		this.querySecleReset();
	}
	//页码重置
	querySecleReset() {
		this.custProgressBean.countNum = 10;
		this.custProgressBean.pageNum = 1;
		this.first = 0;
	}
	//分页
	paginate(event) {
		this.custProgressBean.countNum = event.rows;
		let currentPage = event.page + 1;
		this.custProgressBean.pageNum = currentPage;
		this.first = (currentPage - 1) * this.custProgressBean.countNum;
		this.query();
		this.custProgressBean.pageNum = 1;
	}
	//开始日期
	showDataStart() {
		if (this.startDate && this.thruDate) {
			if (this.startDate > this.thruDate) {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
				setTimeout(() => {
					this.startDate = null;
				})
			}
		}
	}
	//结束日期
	showDataEnd() {
		if (this.startDate && this.thruDate) {
			if (this.startDate > this.thruDate) {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
				setTimeout(() => {
					this.thruDate = null;
				})
			}
		}
	}
	//手动输入日期时，失去焦点时进行判断
	onchange1() {
		this.showDataStart()
	}
	onchange2() {
		this.showDataEnd()
	}
	//返回按钮
	returnBtn() {
		setTimeout(() => {
			window.history.go(-1);
		}, 500)
	}

	compileStr(cust){
		return this.commfunc.compileStr(cust)
	  }
}
